import { ChainId, getAccountArg, getOrderArg, getOrderBookArg, WsTopicType } from 'loopring-sdk';
import { LoopringAPI } from '../stores/apis/api';
import store from '../stores';
import { updateSocketURL } from '../stores/system';
import { tickerService } from './tickerService';
import { ammPoolService } from './ammPoolService';
import { TokenVolumeV3 } from 'loopring-sdk/dist/defs/loopring_defs';

export type socketEventMap = { fn: (e: MessageEvent, props?: { [ key: string ]: any }) => any, deps?: any[] }

export enum SocketEventType {
    pingpong = 'pingpong',
    account = "account",
    order = "order",
    orderbook = "orderbook",
    trade = "trade",
    ticker = "ticker",
    candlestick = "candlestick",
    ammpool = "ammpool"
}

