export type ITokenInfoItem = {
    token: string,
    detail: {
        price: string,
        symbol: string,
        updatedAt: number
    }
}

export type TrendDataItem = {
    timeStamp: number;
    close: number;
}

const AssetPanel = withTranslation('common')(({t, ...rest}: WithTranslation) => {
    const container = useRef(null);
    const [pageSize, setPageSize] = useState(10);

    const [chartPeriod, setChartPeriod] = useState('24 H')

    const [chartData, setChartData] = useState<TrendDataItem[]>([])
    
    // console.log(walletLayer2)
    const { walletLayer2 } = store.getState().walletLayer2;
    const { ammMap } = store.getState().amm.ammMap

    useEffect(() => {
        (async function getUserApi () {
            if (LoopringAPI && LoopringAPI.walletAPI && walletLayer2) {
                const userAssets = await LoopringAPI.walletAPI?.getUserAssets({
                    wallet: '0xeF041462825bFdF79b2f1f02A70b2753cB5b1516',
                    assetType: AssetType.DEX,
                    limit: 30 // TODO: minium unit is day, discuss with pm laters
                })
                if (userAssets.userAssets.length && !!userAssets.userAssets.length) {
                    setChartData(userAssets.userAssets.map(o => ({
                        timeStamp: Number(o.createdAt),
                        close: Number(o.amount)
                    })))
                }
            }
        })()
    }, [walletLayer2])

    useEffect(() => {
        // @ts-ignore
        let height = container?.current?.offsetHeight;
        if (height) {
            setPageSize(Math.floor((height - 120) / 44) - 1);
        }
    }, [container, pageSize]);
    
    const {
        ShowDeposit,
        ShowTransfer,
        ShowWithdraw,
        ShowResetAccount,
    } = useModals()

    const { updateWalletLayer1 } = useWalletLayer1()

    const onShowDeposit = useCallback(() => {
        updateWalletLayer1()
        ShowDeposit(true)
    }, [])

    const onShowTransfer = () => {
        ShowTransfer(true)
    }

    const onShowWithdraw = () => {
        ShowWithdraw(true)
    }

    const AssetTitleProps: AssetTitleProps = {
        assetInfo: {
            // totalAsset: 123456.789,
            totalAsset: !!chartData.length ? chartData[chartData.length - 1].close : 0,
            priceTag: PriceTag.Dollar,
        },
        onShowDeposit,
        onShowTransfer,
        onShowWithdraw,
    }

    const { tokens: tokensAll } = useGetTokens()

    const tokens = tokensAll?.tokenSymbolMap

    // const { balances } = useGetUserBalances(tokens)

    // const { walletLayer2 } = store.getState().walletLayer2;
    const { faitPrices } = store.getState().system

    const tokenPriceList = faitPrices ? Object.entries(faitPrices).map(o => ({
        token: o[ 0 ],
        detail: o[ 1 ]
    })) as ITokenInfoItem[] : []


    const tokenMap = Object.entries(store.getState().tokenMap.tokenMap || {}).map(o => ({
        token: o[ 0 ],
        tokenDetail: o[ 1 ]
    }))
    const coinList = walletLayer2 ? Object.keys(walletLayer2) : []
    const coinDetailList = walletLayer2 ? Object.values(walletLayer2) as any : []

    const doughnutData = coinList.map((coin, index) => {
        const isLpToken = coin.split('-').length === 3
        const result = coin.split('-')
        result.splice(0, 1, 'AMM')
        const ammToken = isLpToken ? result.join('-') : ''
        const ammTokenList = Object.keys(ammMap)
        const tokenPriceUSDT = Number(tokenPriceList.find(o => o.token === coin)?.detail.price) / Number(tokenPriceList.find(o => o.token === 'USDT')?.detail.price)
        
        return ({
            name: coin,
            // TODO: LP TOKEN use 'hooks/help/makeMyAmmWithStat'
            value: ammToken
                ? ammTokenList.includes(ammToken) && ammMap[ammToken] ? ammMap[ammToken].amountDollar : 0
                : Number(volumeToCount(coin, coinDetailList[index].total)) * tokenPriceUSDT
        })
    })

    // TODO: cannot get lp token or smallbalances for now
    const assetsRawData = coinList.map((coin, index) => {
        const tokenInfo = coinDetailList[ index ]
        const tokenPriceUSDT = Number(tokenPriceList.find(o => o.token === coin)?.detail.price) / Number(tokenPriceList.find(o => o.token === 'USDT')?.detail.price)
        const amount = Number(volumeToCount(coin, tokenInfo.total))
        const locked = Number(volumeToCount(coin, tokenInfo.locked))

        return ({
            token: {
                type: tokenMap.find(o => o.token === coin)?.tokenDetail.isLpToken ? TokenType.lp : TokenType.single,
                value: coin
            },
            amount: amount,
            available: amount - locked,
            locked: locked,
            smallBalance: tokenPriceUSDT * Number(volumeToCount(coin, coinDetailList[index].total)) < 1,
        })
    })

    return (
        <>
            <Box>
                <AssetTitle  {...{
                    t,
                    ...rest,
                    ...AssetTitleProps
                }} />
            </Box>

            {/*<div className="title">{t('labelAssetsTitle')}</div>*/}

            <StyledChartWrapper display={'flex'} justifyContent={'space-between'} alignItems={'center'} marginTop={2}>
                <Paper component={'div'}>
                    <Typography component="span" color="textSecondary" variant="body1">Asset Distribution</Typography>
                    <DoughnutChart data={doughnutData}/>
                </Paper>
                <Paper component={'div'}>
                    <Typography component="span" color="textSecondary" variant="body1">Total Assets</Typography>
                    <ScaleAreaChart type={ChartType.Trend} data={chartData}/>
                    <StyledBtnGroupWrapper>
                        <ToggleButtonGroup exclusive size="small" {...{
                            ...rest,
                            t,
                            data: toggleData,
                            value: chartPeriod,
                            setValue: setChartPeriod
                        }} />
                    </StyledBtnGroupWrapper>
                </Paper>
            </StyledChartWrapper>
            <StylePaper marginTop={2}>
                <div className="tableWrapper" ref={container}>
                    <AssetsTable {...{
                        rawData: assetsRawData,
                        pagination: {
                            pageSize: pageSize
                        },
                        showFiliter: true,
                        ...rest
                    }} />
                </div>
            </StylePaper>
        </>
    )
})

export default AssetPanel
