import { all, call, fork, put, takeLatest } from "redux-saga/effects"
import { getTicker, getTickers, getTickerStatus } from './reducer'
import {
    CoinKey,
    CustomError,
    ErrorMap,
    FloatTag,
    PairKey,
    TradeFloat
} from '@loopring-web/component-lib/src/static-resource'

// import { asyncGetTradingInfo, getTradingInfo } from "stores/trading/hook"
import { TickerData, getTokenInfoById, toBig } from "loopring-sdk"

// import { makeCoinInfo } from 'utils/swap_calc_utils'
import { exchangeAPI } from "stores/apis/api"
import { volumeToCount, volumeToCountAsBigNumber } from '../../hooks/help/volumeToCount';
import { Ticker } from './interface';
import store from '../index';

type TickerMap<R extends {[key:string]:any}> = {
    [key in CoinKey<R>|PairKey<R>]?:TradeFloat & {
    reward?:number,rewardToken?:string
    }

}

const getTickersApi = async <R extends {[key:string]:any}>(list:Array<keyof R>)=> {
    const {faitPrices,forex} = store.getState().system
    const tickers = await exchangeAPI().getMixTicker({market: list.join(',')})
    const data:TickerMap<R> = Reflect.ownKeys(tickers.tickMap).reduce((prev, key ) => {
        const item: TickerData = tickers.tickMap[key as any];
        if(item && item.base && forex && faitPrices && faitPrices[item.base]) {
            const volume = volumeToCountAsBigNumber(item.base,item.base_token_volume);
            const priceDollar = volume?.times(faitPrices[item.base].price);
            const priceYuan = priceDollar?.times(forex);
            const change = item.change? item.change * 100 : undefined;

            prev[key as any] = {
                // ...item,
                timeUnit: '24h',
                priceDollar:priceDollar?.toNumber(),
                priceYuan:priceYuan?.toNumber(),
                volume: volume?.toNumber(),
                change: change,
                floatTag: item.close > item.open ? 'increase' : 'decrease',

                // APY: 0,
                reward: 0,
                rewardToken: '',
                __rawTicker__: item,
            } as Ticker;
        }






        // volume: toBig(item.base_token_volume).toNumber(),
        
        return prev
    },{} as TickerMap<R>)

    return { data }
}

export function* getPostsSaga({payload}:any) {
    try {
        // @ts-ignore
        const { tickerKey,tickerKeys } = payload;
        console.log('getPostsSaga tickerKey',tickerKey, tickerKeys)
        if(tickerKey || (tickerKeys && tickerKeys.length)) {
            const {data}  = yield call(getTickersApi,tickerKey?[tickerKey]:tickerKeys);
            yield put(getTickerStatus({tickerMap:data}));

        }else{
            throw new CustomError(ErrorMap.NO_TOKEN_KEY_LIST);
        }
    } catch (err) {
        yield put(getTickerStatus(err));
    }
}

function* tickerSaga() {
    yield all([takeLatest(getTicker, getPostsSaga)]);
}
function* tickersSaga() {
    yield all([takeLatest(getTickers, getPostsSaga)]);
}

export const tickerForks = [
    fork(tickerSaga),
    fork(tickersSaga),
]
 